/* Slider carousel */
.carousel-caption {
  position: absolute;
  z-index: 1;
  display: table;
  width: 100%;
  height: 100%;
}

.carousel-caption div {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

.trickcenter {
  font-weight: bold;
  font-size: 44px;
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  transform: translate(-15%, -50%);
}
.trickcenter_button {
  font-weight: bold;
  font-size: 44px;
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  transform: translate(-15%);
}
.trickcenter_text {
  font-weight: bold;
  font-size: 42px;
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  transform: translate(-15%, 19.5%);
}

/* Slideshow container */
@media screen {
  .slideshow-container {
    max-width: 70%;
    max-height: 600px;
    position: absolute;
    left: 15%;

    margin: auto;
  }

  /* Next & previous buttons */
  .prev,
  .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    padding: 16px;
    margin-top: -22px;
    color: white;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
  }

  /* Position the "next button" to the right */
  .next {
    right: 0;
    border-radius: 3px 0 0 3px;
  }

  /* On hover, add a black background color with a little bit see-through */
  .prev:hover,
  .next:hover {
    background-color: rgba(0, 0, 0, 0.8);
  }

  /* Caption text */
  .text {
    color: #f2f2f2;
    font-size: 43px;
    padding: 8px 12px;
    position: absolute;
    bottom: 50%;
    width: 100%;
    font-weight: bold;
    text-align: center;
  }

  /* Number text (1/3 etc) */
  .numbertext {
    color: #f2f2f2;
    font-size: 12px;
    padding: 8px 12px;
    position: absolute;
    top: 0;
  }

  /* The dots/bullets/indicators */
  .dot {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
  }

  /* Fading animation */
  .fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }

  @-webkit-keyframes fade {
    from {
      opacity: 0.4;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes fade {
    from {
      opacity: 0.4;
    }
    to {
      opacity: 1;
    }
  }

  /* On smaller screens, decrease text size */
  @media only screen and (max-width: 300px) {
    .prev,
    .next,
    .text {
      font-size: 11px;
    }
  }
}

